'use client'

import { useState } from 'react'
import toast from 'react-hot-toast'
import { useAuth } from '@/context/AuthProvider'
import { useProtectRoute } from '@/lib/auth'
import FormInput from '@/components/FormInput'
import FileUpload from '@/components/FileUpload'

type OtherDoc = { title: string; file: File | null }

export default function ApplyPage() {
  useProtectRoute()
  const { user } = useAuth()

  const [form, setForm] = useState({
    pfa: '',
    rsa_balance: '',
    bvn: '',
    nin: '',
    dob: '',
    phone1: '',
    phone2: '',
    alt_email: '',
  })

  const [requiredFiles, setRequiredFiles] = useState<Record<string, File | null>>({
    account_opening_form: null,
    nin_slip: null,
    undertaken_form: null,
    consent_form: null,
    indemnity_form: null,
    application_letter: null,
  })

  const [otherDocs, setOtherDocs] = useState<OtherDoc[]>([{ title: '', file: null }])

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setForm({ ...form, [e.target.name]: e.target.value })
  }

  const handleFileChange = (name: string, file: File | null) => {
    setRequiredFiles({ ...requiredFiles, [name]: file })
  }

  const handleOtherDocsChange = (index: number, field: 'title' | 'file', value: any) => {
    const updated = [...otherDocs]
    updated[index][field] = value
    setOtherDocs(updated)
  }

  const addOtherDoc = () => {
    setOtherDocs([...otherDocs, { title: '', file: null }])
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()

    const formData = new FormData()
    Object.entries(form).forEach(([key, value]) => {
      formData.append(key, value)
    })

    Object.entries(requiredFiles).forEach(([key, file]) => {
      if (file) formData.append(key, file)
    })

    otherDocs.forEach((doc, i) => {
      if (doc.file && doc.title) {
        formData.append(`other_docs[${i}][title]`, doc.title)
        formData.append(`other_docs[${i}][file]`, doc.file)
      }
    })

    try {
      const res = await fetch('http://localhost:5000/api/user/profile', {
        method: 'POST',
        credentials: 'include',
        body: formData,
      })

      const data = await res.json()
      if (!res.ok) throw new Error(data.message || 'Something went wrong')

      toast.success('Application submitted successfully')
    } catch (err: any) {
      toast.error(err.message || 'Submission failed')
    }
  }

  return (
    <div className="max-w-3xl mx-auto px-4 py-10">
      <h2 className="text-2xl font-semibold mb-6">Submit Application</h2>

      <form onSubmit={handleSubmit} className="space-y-5">
        <FormInput name="first_name" value={user?.first_name || ''} disabled />
        <FormInput name="surname" value={user?.surname || ''} disabled />
        <FormInput name="other_names" value={user?.other_names || ''} disabled />

        <FormInput name="pfa" value={form.pfa} onChange={handleChange} placeholder="PFA" required />
        <FormInput name="rsa_balance" value={form.rsa_balance} onChange={handleChange} placeholder="RSA Balance" required />
        <FormInput name="bvn" value={form.bvn} onChange={handleChange} placeholder="BVN" required />
        <FormInput name="nin" value={form.nin} onChange={handleChange} placeholder="NIN" required />
        <FormInput name="dob" value={form.dob} onChange={handleChange} placeholder="Date of Birth" type="date" required />
        <FormInput name="phone1" value={form.phone1} onChange={handleChange} placeholder="Phone Number 1" required />
        <FormInput name="phone2" value={form.phone2} onChange={handleChange} placeholder="Phone Number 2" required />
        <FormInput name="alt_email" value={form.alt_email} onChange={handleChange} placeholder="Alt Email" type="email" required />

        <h3 className="font-semibold">Upload Required Documents</h3>
        {Object.keys(requiredFiles).map((key) => (
          <FileUpload
            key={key}
            name={key}
            label={key.replace(/_/g, ' ').replace(/\b\w/g, (c) => c.toUpperCase())}
            onChange={(file) => handleFileChange(key, file)}
          />
        ))}

        <h3 className="font-semibold mt-6">Other Documents (Optional)</h3>
        {otherDocs.map((doc, index) => (
          <div key={index} className="grid grid-cols-2 gap-4 items-end">
            <FormInput
              name={`other_docs[${index}][title]`}
              value={doc.title}
              onChange={(e) => handleOtherDocsChange(index, 'title', e.target.value)}
              placeholder="Document Title"
            />
            <FileUpload
              name={`other_docs[${index}][file]`}
              label="Upload file"
              onChange={(file) => handleOtherDocsChange(index, 'file', file)}
            />
          </div>
        ))}
        <button type="button" onClick={addOtherDoc} className="btn btn-outline">
          Add Another Document
        </button>

        <button type="submit" className="btn btn-primary w-full mt-4">
          Submit Application
        </button>
      </form>
    </div>
  )
}
